import React from 'react';
import './Instructions.css';

const Quantity = () => {
    return (
        <div id='instructions'>
            <p>Nice! How many? (Or just say zero to cancel) <i className='fas fa-hand-point-down'></i></p>
        </div>
    )
}

export default Quantity;


